<script>
  import { createEventDispatcher } from "svelte";
  import { onMount } from "svelte";

  export let label = "";
  export let colorHue = 180;
  export let isActive = false;
  export let materialIcon = "";

  onMount(() => {
    setButtonColors(isActive);
  });

  const dispatch = createEventDispatcher();

  function dispatchType() {
    dispatch("dispatchType", {
      selectedType: label
    });
  }

  function setButtonColors(buttonActive) {
    if (buttonActive) {
      document
        .getElementById(label + "-button")
        .style.setProperty(
          "background-color",
          "hsl(" + colorHue + ", 50%, 80%)"
        );
      document
        .getElementById(label + "-button")
        .style.setProperty("color", "hsl(" + colorHue + ", 65%, 40%)");

      document
        .getElementById(label + "-label")
        .style.setProperty("color", "hsl(" + colorHue + ", 65%, 55%)");
    } else {
      document
        .getElementById(label + "-button")
        .style.setProperty("background-color", "var(--inactive-button-color)");
      document
        .getElementById(label + "-button")
        .style.setProperty("color", "var(--text-color2)");

      document
        .getElementById(label + "-label")
        .style.setProperty("color", "var(--text-color2)");
    }
  }

  $: if (document.getElementById(label + "-button")) {
    setButtonColors(isActive);
  }
</script>

<style type="text/postcss">.wrapper{display:flex;flex-direction:column;align-items:center;justify-content:center;width:33.33333%;margin-top:1rem;color:var(--text-color);transition-timing-function:ease-in;transition:all .35s}.type-button{border-radius:9999px;padding:.5rem;fill:currentColor;text-align:center;vertical-align:middle;background-color:var(--inactive-button-color);color:var(--text-color2)}.type-button i{display:block!important}</style>

<div class="wrapper">
  <button
    class="type-button"
    title={label}
    id="{label}-button"
    on:click={() => dispatchType()}>
    <i class="material-icons">{materialIcon}</i>
  </button>
  <span id="{label}-label">{label}</span>
</div>
